随之缓慢增加，当活性炭吸附饱和时，设备阻力达到较大值，此后的设备净化效率基本失去。为此，系统在设备进出风口处设置一套差压测量系统，对该装置进出口的废气压力差进行检测并显示，及时更换活性炭。\n        由于活性炭具有能够再生的特点，在活性炭用量大、废气浓度高及活性炭吸附饱和时间短的情况下，可使用活性炭脱附装置对活性炭进行再生循环使用（脱附介质可使用蒸汽）。使用活性炭脱附装置不但可以节约运行成本，而且能够对吸附的废气进行回收，同时避免了二次污染。在不回收溶剂的情况下，还可以选用光催化氧化活性炭再生装置对活性炭进行再生循环使用。汽车喷漆房废气处理该设备核心中的纳米光催化触媒材料（GC-100）是一种吸收光能后，能在其表面产生催化反应的物质，其功能类似于植物的叶绿素。当特定纳米波长的紫外光照射光催化触媒材料（GC-100）时，其表面发生光催化氧化还原反应。终形成二氧化碳和水。\n      ")]),_vm._v(" "),_c('img',{attrs:{"src":"http://www.wlj.com.cn/WebEditor/uploadfile/20200921090358451.jpg","alt":""}})])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('span',[_vm._v("\n        下一篇：\n        "),_c('a',{attrs:{"href":""}},[_vm._v("走进鹏辉前程，学习先进经验")])])}]
